.navs .navbar-link-css {
  padding-left: 5vh;
  padding-right: 5vh;
}
.navbar-link-css:hover{
  color: #000;
}
.nav_active{
  color: #000;
}

.headings-logos-buttons .nav-dynamic-button {
  display: none;
  margin: 5px;
}

@media (max-width: 1308px) {
  .navs .navbar-link-css {
    padding-left: 4vh;
    padding-right: 4vh;
  }
}

.justify-content-between #hidden {
  display: flex;
}

@media (max-width: 1140px) {
  .navs .navbar-link-css {
    padding-left: 3vh;
    padding-right: 3vh;
  }
}

@media (max-width: 985px) {
  .navs .navbar-link-css {
    padding-left: 2vh;
    padding-right: 2vh;
  }
}

@media (max-width: 820px) {
  .navs .navbar-link-css {
    padding-left: 1vh;
    padding-right: 1vh;
  }
}

@media (max-width: 685px) {
  .navs {
    display: none;
  }

  .headings-logos-buttons .nav-dynamic-button {
    display: inline;
  }

  .justify-content-between #hidden {
    position: absolute;
    left: 0px;
    top: 65px;
    height: 250px;
    max-height: 250px;
    width: 100%;
    background-color: #01bfd9;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 2;
  }

  .justify-content-between #hidden .navbar-link-css {
    margin: 10px !important;
  }
}
